body {
  margin: 0;
}

li {
  list-style: none;
}

h2, p, ul {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.team {
  background-color: rgba(165, 158, 158, .54);
}

.team-card {
  width: 500px;
  height: 550px;
  scroll-snap-type: x mandatory;
  background: #837f7f;
  border-radius: 8px;
  gap: 12px;
  margin-left: auto;
  margin-right: auto;
  padding: 24px;
  scroll-padding: 24px;
  display: flex;
  overflow-x: scroll;
  box-shadow: 0 8px 30px rgba(66, 60, 62, .1);
}

.team-title {
  text-align: center;
  color: #fff;
}

.team-box {
  width: 350px;
  padding-left: 75px;
}

.team-text {
  scroll-snap-align: start;
  color: #fff;
  border-radius: 8px;
  flex: 0 0 100%;
  padding-left: 0;
  padding-right: 24px;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, .2);
  transition: visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 550px;
  height: 600px;
  z-index: -1;
  background-color: #fff;
  border-radius: 4px;
  transition: transform .25s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal-close-btn {
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-close-btn:hover, .modal-close-btn:focus {
  fill: #ff6b01;
}

